<template>
  <v-container>
    <v-row class="main-head text-h5 pa-3 mb-3">배송지 관리</v-row>
    <v-divider />
    <v-row>
      <v-col cols="12" v-for="addr in addrlist" :key="addr.ano" class="pa-4">
        <v-row no-gutters>
          <v-col cols="12" class="font-weight-bold text-sm-h5">{{ addr.aname }}<span v-if="addr.isdefault == 'Y'"> [기본 배송지]</span></v-col>
          <v-col cols="3">{{ addr.toname }}</v-col>
          <v-col cols="9" align="light">{{ addr.tophone }}</v-col>
        </v-row>
        <v-row no-gutters>
          <v-col cols="3">[{{ addr.addr1 }}]</v-col>
          <v-col cols="9 ">{{ addr.addr2 }}<span class="ml-2"></span>{{ addr.addr3 }}</v-col>
        </v-row>
        <v-row no-gutters>
          <v-col cols="12" class="text-right">
            <Address
              :title="'수정'"
              :btncolor="'green lighten-2'"
              :content="'배송지 수정'"
              :setcnt="100"
              :toname="addr.toname"
              :tophone="addr.tophone"
              :addr1="addr.addr1"
              :addr2="addr.addr2"
              :addr3="addr.addr3"
              :aname="addr.aname"
              :addDefault="addr.isdefault == 'Y' ? 'YES' : 'NO'"
              :ano="addr.ano"
              @inform="modify"
            />
            <yes-no-dialog @choose="addrDelete(addr.ano)" :title="'삭제'" :content="'정말로 삭제하시겠습니까?'" :btntype="'red'" />
          </v-col>
        </v-row>
        <v-divider class="mt-2" />
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" align="right" v-if="addrlist.length <= 5">
        <Address :title="'배송지 추가'" @inform="add" />
      </v-col>
      <v-col v-else align="right">
        <v-btn color="primary" @click="failInform" small>배송지 추가</v-btn>
      </v-col>
    </v-row>
    <div class="text-center ma-2">
      <v-snackbar v-model="snackbar">
        {{ resultInfo }}
        <template v-slot:action="{ attrs }">
          <v-btn color="pink" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-container>
</template>

<script>
import MemberApi from "@/apis/member";
import Address from "@/components/tool/Address.vue";
import YesNoDialog from "@/components/dialog/YesNoDialog.vue";
export default {
  components: { Address, YesNoDialog },
  name: "MyAddress",
  data: () => ({
    addrlist: [],
    addr: {},
    snackbar: false,
    resultInfo: "",
  }),
  methods: {
    async requestAddrList(mno) {
      try {
        const promise = await MemberApi.getAddress(mno);
        this.addrlist = promise.data;
      } catch (error) {
        console.log(error);
      }
    },
    async add(aname, addr1, addr2, addr3, toname, tophone) {
      this.addr["aname"] = aname;
      this.addr["addr1"] = addr1;
      this.addr["addr2"] = addr2;
      this.addr["addr3"] = addr3;
      this.addr["toname"] = toname;
      this.addr["tophone"] = tophone;
      this.addr["isdefault"] = "N";
      this.addr["mno"] = this.$store.getters.getMno;
      try {
        const promise = await MemberApi.addAddress(this.addr, "");
        if (promise.data.result == "success") {
          this.requestAddrList(this.$store.getters.getMno);
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.clear();
      }
    },
    clear() {
      for (let key in this.addr) {
        this.addr[key] = "";
      }
    },
    failInform() {
      this.resultInfo = "더이상 배송지를 추가할 수 없습니다.";
      this.snackbar = true;
    },
    async addrDelete(val) {
      const promise = await MemberApi.deleteAddress(this.$store.getters.getMno, val);
      if (promise.data == "success") {
        console.log("삭제 성공");
        this.requestAddrList(this.$store.getters.getMno);
      }
    },
    async modify(aname, addr1, addr2, addr3, toname, tophone, isDefault, ano) {
      let now = {};
      for (let key in this.addrlist) {
        if (this.addrlist[key].ano == ano) {
          now = this.addrlist[key];
          break;
        }
      }
      const newAddr = {};
      newAddr["aname"] = aname;
      newAddr["addr1"] = addr1;
      newAddr["addr2"] = addr2;
      newAddr["addr3"] = addr3;
      newAddr["toname"] = toname;
      newAddr["tophone"] = tophone;
      newAddr["isdefault"] = isDefault;
      newAddr["ano"] = ano;
      newAddr["mno"] = this.$store.getters.getMno;

      for (let key in now) {
        if (newAddr[key] != now[key]) {
          // 업데이트 실행 - 만약 isDefault가 트루일 경우 isDefault인 친구 N으로 바꾸는거 먼저 진행
          try {
            const promise = await MemberApi.modifyAddress(newAddr);
            console.log(promise.data);
            if (promise.data == "success") {
              this.requestAddrList(this.$store.getters.getMno);
              this.resultInfo = "수정되었습니다.";
              this.snackbar = true;
            }
          } catch (error) {
            console.log(error);
          }
          break;
        }
      }
    },
  },
  created() {
    this.requestAddrList(this.$store.getters.getMno);
  },
};
</script>

<style></style>
