<template>
  <div class="d-inline-block">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on, attrs }">
        <v-btn :color="btntype ? btntype : 'primary'" dark v-bind="attrs" v-on="on" small> {{ title }} </v-btn>
      </template>
      <v-card>
        <v-card-title class="text-h5"> {{ title }} </v-card-title>
        <v-card-text>{{ content }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" text @click="close('N')"> No </v-btn>
          <v-btn color="blue darken-1" text @click="close('Y')"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "YesNoDialog",
  data: () => ({
    dialog: false,
  }),
  methods: {
    close(val) {
      if (val == "Y") {
        this.$emit("choose");
      }
      this.dialog = false;
    },
  },
  props: ["title", "content", "btntype"],
};
</script>

<style></style>
